import React from "react";
import "./Spinner.css";
export default function Spinner({ heightPrm, widthPrm, borderTopPrm, color }) {
  return (
    <div
      className="loader"
      style={{
        height: heightPrm ? heightPrm : 15,
        width: widthPrm ? widthPrm : 15,
      }}
    />
  );
}
