import React from "react";
import "./Button.css";
import Spinner from "../Spinner/Spinner";
import { setBin, getBin } from "../Actions/Actions";
import shilshul from "./shilshul.png";
export default function Feed() {
  const [status, setStatus] = React.useState(0);
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(-1);

  async function statusSetter() {
    const setRes = await setBin(status > 0 ? -1 : 1);
    if (setRes && setRes === 0) setError("התרחשה שגיאה");
  }
  async function statusGetter() {
    const fetchRes = await getBin();
    setLoading(false);
    if (fetchRes !== 0) setStatus(fetchRes);
    else if (fetchRes === 0) setError("התרחשה שגיאה");
  }
  React.useEffect(() => {
    if (status === 0) statusGetter();
  }, []);

  return (
    <div className="feed_container">
      {loading && (
        <div className="spinner_container">
          <Spinner />
        </div>
      )}
      {!loading && error === 0 && (
        <div className="error_container">{error}</div>
      )}
      {!loading && (
        <div
          className="garvishon"
          style={{
            border: status > 0 ? "1px solid green" : "1px solid red",
          }}
        >
          <div className="garvishon_image">
            <img className="image" src={shilshul} />
          </div>
          <br />
          <br />
          <div
            onClick={() => {
              setStatus(status > 0 ? -1 : 1);
              statusSetter();
            }}
            className="garvishon_footer"
            style={{
              backgroundColor: status > 0 ? "green" : "red",
              color: "white",
            }}
          >
            {status > 0 ? "ALIVE" : "DEAD"}
          </div>
        </div>
      )}
    </div>
  );
}
