import axios from "axios";
const url =
  "https://jsonstorage.net/api/items/7a217064-3470-4bf5-9829-bba4778c1452";

export const getBin = async () => {
  return new Promise((resolve) => {
    axios({
      method: "GET",
      url: url,
    })
      .then((response) => {
        const { status } = response.data;
        if (status !== undefined) resolve(status);
        else resolve(0);
      })
      .catch((e) => {
        console.log(e);
        resolve(0);
      });
  });
};

export const setBin = async (newStatus) => {
  return new Promise((resolve) => {
    axios({
      method: "PUT",
      url: url,
      data: { status: newStatus },
      headers: { "Content-Type": "application/json; charset=utf-8" },
    })
      .then((response) => {
        if (response && response.data) resolve(1);
        else resolve(0);
      })
      .catch((e) => {
        console.log(e);
        resolve(0);
      });
  });
};
